import Modal from './confirm';
import { last, dropRight, } from 'lodash';

function setupDxbModal(initVueOpt = {}) {
    let modalInstance = [];
    function getModalInstance(opt, resolve = null, reject = null) {
        let props = Object.assign({}, opt, {
            closable: false,
            maskClosable: false,
            footerHide: true, // Modal.vue 
        });
        let newInstance = Modal.newInstance(initVueOpt, props, resolve, reject);
        modalInstance.push(newInstance);
        // console.log('modalInstance newInstance',modalInstance)
        return last(modalInstance);
    }

    function confirm(options, resolve = null, reject = null) {
        let instance = getModalInstance(options, resolve, reject);

        options.onRemove = function () {
            // 
            modalInstance = dropRight(modalInstance) || [];
            // console.log('modalInstance onRemove',modalInstance)
        };

        instance.show(options);
    }

// promise  buttonLoading [],promise
    Modal.confirm = function ({
                                  width = 500, // modal 
                                  marginTB = 20, // body 5, 10, 15, 20, 24, 26, 28
                                  loading = false, // 
                                  render = undefined, //  ---body
                                  content = null, // body HTML ---body
                                  props = {}, // props
                                  headTitle = '', // 
                                  headRender = undefined, //   --- headRender title
                                  footerRender = undefined, //   --- footerRender  footerShowshowCancelshowOk 
                                  footerProps = {}, // props
                                  footerShow = true, // , render $emit('ok',{}) $emit('quit')
                                  lockScroll = true, // 
                                  okType = 'primary', // ok   footerShow true 
                                  showOk = true, // ok
                                  showCancel = true, // cancel
                                  okText = null, // ok
                                  cancelText = null, // cancel
                                  closable = false, // 
                                  fullscreen= false, // 
                                  scrollable = false, // 
                                  className = '', // 
                                  styles = {}, // 
                                  onOk = () => {
                                  },
                                  onCancel = () => {
                                  },
                                  zIndex = 1000,
                              }, isPromise = false) {
        let _props = {
            width,
            lockScroll,
            render,
            body: content,
            footerRender,
            footerShow: footerRender || footerShow,
            headRender,
            okType,
            headTitle,
            props,
            showOk,
            okText,
            cancelText,
            showCancel,
            loading: isPromise ? false : loading,
            onOk,
            onCancel,
            closable,
            fullscreen,
            scrollable,
            styles,
            className,
            marginTB,
            zIndex
        };
        if (isPromise) {
            return new Promise((resolve, reject) => {
                confirm(_props, resolve, reject);
            });
        } else {
            return confirm(_props);
        }
    };

    Modal.remove = function () {
        if (!last(modalInstance)) {
            // 
            // console.log('',modalInstance)
            return false;
        }

        const instance = getModalInstance();

        instance.remove();
        // console.log(' ',modalInstance)
    };
    return Modal;
}

export default setupDxbModal;




